  <div class="nav">
    <div class="container-fluid noselect">
      <div
        class="
          white-bar
          d-flex
          justify-content-between
          align-items-center
          p-0
          m-0
        "
      >
        <div class="name other-buttons">
          <router-link to="/">
            <p class="pages-button">Roxana Kenjeeva</p>
          </router-link>
        </div>

        <div class="flex-grow-1 mx-3">
          <img src="@/img/nav/sketch-line-left.png" class="img-fluid" alt="" />
        </div>

        <div
          class="menu-button other-buttons"
          @click="showMenu = !showMenu"
          
        >
          <svg :class="{ menurotated: showMenu }" class="menu-bars" viewBox="0 0 130 80" width="30" height="40">
            <rect width="130" height="8"></rect>
            <rect y="30" width="130" height="8"></rect>
            <rect y="60" width="130" height="8"></rect>
          </svg>
        </div>
      </div>
      <transition name="expand">
        <div class="menu p-1" v-if="!!showMenu" @click="showMenu = !showMenu">
          <div
            class="
              d-flex
              flex-column
              other-buttons
              justify-content-end
              flex-wrap
              mx-2
            "
          >
            <router-link to="/about" tag="div" class="text-start"
              ><p class="menu-item">About</p>
            </router-link>

            <router-link
              to="/projects"
              tag="div"
              class="text-start"
              :class="{ 'router-link-active': onProjects }"
              ><p class="menu-item line">Projects</p></router-link
            >
            <router-link
              to="/shop"
              tag="div"
              class="text-start"
              :class="{ 'router-link-active': onShop }"
              ><p class="menu-item">Shop</p></router-link
            >
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showMenu: false,
      onProjects: false,
      onShop: false,
    };
  },
  methods: {
    subIsActive() {
      const path = this.$route.path;

      if (path.includes("/projects")) {
        this.onProjects = true;
      } else {
        this.onProjects = false;
      }
      if (path.includes("/shop")) {
        this.onShop = true;
      } else {
        this.onShop = false;
      }
    },
  },
  watch: {
    $route() {
      this.subIsActive();
    },
    created() {
      this.subIsActive();
    }
  },
};
</script>

