<template>
<div>
  <div class="basket">
    <div class="row justify-content-center">
      <div class="col-1"></div>
      <div class="col-auto m-0 p-0">
        <div class="d-flex justify-content-center align-items-center snipcart-checkout">
          <img
            v-if="basketCount === 0"
            src="@/img/shop/basket.png"
            class="img-fluid basket-image"
            alt=""
          />

          <img
            v-if="basketCount > 0"
            src="@/img/shop/basket-full.png"
            class="img-fluid basket-image"
            alt=""
          />
        </div>
      </div>
      <div class="col-1 d-flex  align-items-center basket-count" ><p class="m-0 p-0" v-if="basketCount > 0"><span>(</span>{{ basketCount }}<span>)</span></p></div>
    </div>
    
  </div>
  <div class="bar"></div></div>
</template>

<script>
export default {
  name: "BasketCount",

  data() {
    return {
      basketCount: 0,
    };
  },
  methods: {
    getTotalPrice() {
      Snipcart.store.subscribe(() => {
        this.basketCount = Snipcart.store.getState().cart.items.count;
      });
    },
  },

  created() {
    document.addEventListener("snipcart.ready", () => {
      Snipcart.events.on("item.added", () => {
        this.basketCount = Snipcart.store.getState().cart.items.count;
      });
      Snipcart.events.on("item.updated", () => {
        this.basketCount = Snipcart.store.getState().cart.items.count;
      });
      this.getTotalPrice();
    });
  },
};
</script>

