<template>
  <div class="container my-md-5 my-0">
    <div class="row justify-content-around gx-5">
      <div class="col-12 col-lg-6 mb-3 mb-md-0 pb-md-0 order-lg-last">
        <div class="row sticky-guy">
          <div class="col-12">
            <SkeletonBox v-if="textLoaded" :min-width="20" :max-width="40" />
            <h6 v-if="!textLoaded" class="m-0 title-text">{{ title }}</h6>
          </div>

          <div class="col-12 my-1 yeartext">
            <SkeletonBox v-if="textLoaded" :min-width="8" :max-width="15" />
            <p v-if="!textLoaded" class="m-0">{{ year }}</p>
          </div>
          <div class="col-12">
            <SkeletonBox v-if="textLoaded" :min-width="10" :max-width="20" />
            <p v-if="!textLoaded" class="yeartext m-0">{{ medium }}</p>
          </div>
          <div class="col-12 mt-4" v-if="!isDesktop">
            <div v-if="textLoaded">
              <SkeletonBox :min-width="100" :max-width="100" />
            </div>
            <div v-if="!textLoaded">
              <p
                v-if="!textExpanded"
                v-snip="1"
                class="text-snipped"
                @click="textExpanded = !textExpanded"
              >
                {{ textCut }}
              </p>
              <prismic-rich-text v-if="textExpanded" :field="text" />
            </div>
          </div>
          <div class="col-12 mt-4" v-if="isDesktop">
            <div v-if="textLoaded">
              <SkeletonBox :min-width="94" :max-width="95" />
              <SkeletonBox :min-width="94" :max-width="95" />
              <SkeletonBox :min-width="94" :max-width="95" />
            </div>
            <prismic-rich-text v-if="!textLoaded" :field="text" />
          </div>
        </div>
      </div>
      <div class="col-12 col-lg-6 mb-4 mb-md-0 order-lg-first">
        
        <vue-plyr class="p-0 mb-5" v-if="thereIsVideo">
          <div 
            data-plyr-provider="vimeo"
            class="p-0 m-0"
            :data-plyr-embed-id="videoId"
          ></div>
        </vue-plyr>
        <div v-if="videoAndImages" class="mb-5 pb-5">
        </div>

        <div v-if="thereAreImages" :class="{ 'mb-5 pb-5': multipleImages }">
          <div
            v-for="(image, index) in gallery"
            :key="index"
            class="photo d-flex justify-content-center"
            :class="{ 'mt-5 pt-5': addPadding(index) }"
          >
            <img
              data-expand="-20"
              :data-src="image.image1.url"
              alt=""
              class="lazyload img-fluid img-background"
              :style="imageRatio(image)"
              :height="image.image1.dimensions.height"
              :width="image.image1.dimensions.width"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectItem",

  data() {
    return {
      isDesktop: true,
      linkResolver: this.$prismic.linkResolver,
      post: [],
      title: [],
      text: [],
      textCut: [],
      year: [],
      field: [],
      gallery: [],
      image: [],
      medium: [],
      thereIsVideo: false,
      thereAreImages: false,
      multipleImages: false,
      videoAndImages: false,
      textLoaded: true,
      videoId: {},
      toggleInfo: false,
      textExpanded: false,
    };
  },
  methods: {
    getContent(uid) {
      //Query to get post content
      this.$prismic.client.getByUID("post", uid).then((post) => {
        if (post) {
          this.title = post.data.title[0].text;
          this.text = post.data.text;
          this.textCut = post.data.text[0].text;
          this.post.image = post.data.image.url;
          this.gallery = post.data.gallery;
          this.videoId = post.data.video.html;
          this.year = post.data.year;
          this.medium = post.data.medium[0].text;
          this.displayVideo();
          this.displayImages();
          this.areThereImagesAndVideo();
          this.windowSize();
          this.textLoaded = false;
          if (this.gallery.length > 1) {
            this.multipleImages = true;
          }
        } else {
          //returns error page
          this.$router.push({ name: "not-found" });
        }
      });
    },
    windowSize() {
      let screenSize = window.innerWidth;
      if (screenSize > 768) {
        this.isDesktop = true;
      } else {
        this.isDesktop = false;
      }
    },

    imageRatio(e) {
      let height = e.image1.dimensions.height;
      let width = e.image1.dimensions.width;
      let ratio = width / height;
      let maxHeight = (window.innerHeight / 100) * 70;

      if (ratio < 1) {
        return {
          "max-height": maxHeight + "px",
          "max-width": maxHeight * ratio + "px",
        };
      } else return;
    },
    addPadding(el) {
      if (el > 0) {
        return true;
      }
    },
    displayVideo() {
      if (this.videoId !== undefined) {
        this.thereIsVideo = true;
      } else this.thereIsVideo = false;
    },
    displayImages() {
      if (this.gallery[0].image1.url !== undefined) {
        this.thereAreImages = true;
      } else this.thereAreImages = false;
    },
    areThereImagesAndVideo() {
      if(this.thereAreImages && this.thereIsVideo) {
        this.videoAndImages = true;
      } else {
        this.videoAndImages = false;
      }
    },
  },
  beforeCreate() {
    this.$nextTick(() => {
      window.addEventListener("resize", () => {
        let screenSize = window.innerWidth;
        if (screenSize > 768) {
          this.isDesktop = true;
        } else {
          this.isDesktop = false;
        }
      });
      window.addEventListener("load", () => {
        let screenSize = window.innerWidth;
        if (screenSize > 768) {
          this.isDesktop = true;
        } else {
          this.isDesktop = false;
        }
      });
    });
  },

  created() {
    this.getContent(this.$route.params.uid);
  },
};
</script>

<style scoped>



.text-snipped:hover {
  cursor: pointer;
}

.title-text {
  font-weight: bold;
  color: rgb(46, 46, 46);
}
.photo {
  scroll-snap-align: start;
  top: 72px;
}

.img-max {
  max-height: 83.5vh;
}

.img-background {
  background-color: rgba(148, 148, 148, 0.63) !important;
}

.sticky-guy {
  position: sticky;
  top: 120px;
}

.yeartext {
  color: grey;
  margin-top: -10px;
}
</style>

